.development > * {
  pointer-events: none;
}

.IndexMenu {
  /* width: 100vw; */
  position: relative;
  height: calc(100vh - (var(--navigation-height) + 18px));
  pointer-events: none;
}

.IndexMenu > * {
  pointer-events: auto;
}

.OrbiteContainer {
  position: absolute;
  pointer-events: none;
  background-color: var(--cool-bg);
}

.icon-approfondimento {
  padding-left: 0;
}

.icon-approfondimento:hover{
  padding-left: -10px !important;
  transition: ease-in 1s;
}

.text-approfondimento {
  display: none;
}

.icon-approfondimento:hover > .text-approfondimento{
  display: block;
  transition: ease-in 1s;
}

.small-approfondimento{
  font-size: 10px;
  color: #808080;
  line-height: 10px;
}

.tappa1 {
  position: absolute;
  /* top: calc(calc(100vh / 5.25) - var(--navigation-height));
  left: calc(100% / 12 * 7); */
  width: 44px;
  height: 44px;
}

.tappa2 {
  position: absolute;
  /* top: calc(calc(100vh / 5.75) - var(--navigation-height));
  left: calc(100% / 12 * 7.5); */
  width: 44px;
  height: 44px;
}

.tappa3 {
  position: absolute;
  top: calc(calc(100vh / 6.25) - var(--navigation-height));
  left: calc(100% / 12 * 8);
  width: 44px;
  height: 44px;
}

.tappe {
  color: var(--dark-green);
  text-transform: uppercase;
  position: absolute;
  top: calc(calc(100vh / 7.5) - var(--navigation-height));
  left: calc(100% / 12 * 7.4);
  font-size: 14px;
  font-weight: bold;
}

.title-viz {
  font-size: 16px;
  font-weight: 700;
}


.icon-width {
  width: 80px;
}


/* Icons position and size */
.territorio .title-viz {
  background: rgb(255,255,250);
  padding:0 0.4em 0.4em 0.4em;
}

.territorio {
  position: absolute;
  top: calc(calc(100vh / 6) - var(--navigation-height));
  left: calc(100% / 12 * 5.5);
  display: flex;
  align-items: center;
  flex-direction: column;
}

